<template>
  <div class="image-canvas">
    <img
      :style="image.style"
      class="image"
      :key="image.id"
      :src="image.src"
      v-for="image in addedImage"
    />
  </div>
</template>

<script>
export default {
  data() {
    return {
      images: [
        require("@/img/randomimages/dinosaur.png"),
        require("@/img/randomimages/orange.png"),
        require("@/img/randomimages/stone.png"),
        // require('@/img/randomimages/leaf.png')
      ],
      addedImage: [],
      imgTop: 150,
      imgLeft: 900,
      imgHeight: 64,
      imgWidth: 64,
      changeInterval: 2000,
      selectedImage: "",
      nextId: 100,
    };
  },
  created() {
 
    this.randomImage();
    const randomImg = (func) => setInterval(func, this.changeInterval);
    randomImg(this.randomImage);
    randomImg(this.addImage);
    randomImg(this.randomPosition);
  },
 
  methods: {
    randomImage() {
      const idx = Math.floor(Math.random() * this.images.length);
      this.selectedImage = this.images[idx];
    },
    randomPosition() {
      const randomPos = (twoSizes) => Math.round(Math.random() * twoSizes);
      this.imgTop = randomPos(document.body.scrollHeight - this.imgHeight);
      this.imgLeft = randomPos(window.innerWidth - this.imgWidth);
    },
    addImage() {
      this.addedImage.push({
        style: {
          top: `${this.imgTop + 60}px`,
          left: `${this.imgLeft}px`,
          height: `${this.imgHeight}px`,
          width: `${this.imgWidth}px`,
        },
        src: this.selectedImage,
        id: this.nextId++,
      });
      if (this.addedImage.length > 5) this.removeItem();
    },
    removeItem() {
      this.addedImage.shift(0);
    },

  },
};
</script>


<style scoped>
.image {
  position: absolute;
  z-index: -200;
}
</style>