<template>
  <div>
    <div class="icons">
      <swiper
        v-if="isDataLoaded"
        :slidesPerView="5"
        :spaceBetween="0"
        :loop="true"
        :slideToClickedSlide="true"
        :centeredSlides="true"
        :initialSlide="page"
      >
        <swiper-slide
          v-for="post in posts"
          :key="post.id"
          :id="post.id"
          class="d-flex justify-content-center"
        >
          <router-link :to="post.uid">
            <img
              class="img-fluid lazyload"
              :data-src="post.data.icon.url"
              alt=""
          /></router-link>
        </swiper-slide>
      </swiper>
    </div>
  </div>
</template>

<script>
import { Swiper, SwiperSlide } from "swiper/vue";
import "swiper/swiper-bundle.css";

export default {
  name: "ProjectLooper",
  components: {
    Swiper,
    SwiperSlide,
  },

  data() {
    return {
      linkResolver: this.$prismic.linkResolver,
      posts: [],
      icon: [],
      otherPages: [],
      amountOfPosts: null,
      nextIndex: 0,
      previousIndex: 0,
      currentPage: "",
      pageComingUp: "",
      uidNext: "",
      uidPrevious: "",
      page: 0,
      isDataLoaded: false,
    };
  },
  methods: {
    getPosts() {
      //Query to get blog posts
      this.$prismic.client
        .query(this.$prismic.Predicates.at("document.type", "post"), {
          orderings: "[my.post.year desc ]",
        })
        .then((response) => {
          this.posts = response.results;
          const myPosts = this.posts.map((row) => row.uid);

          for (var i = 1; i < myPosts.length; i++) {
            this.otherPages = myPosts;
          }
          this.amountOfPosts = this.otherPages.length;
          this.updateIcon();
        });
    },
    updateIcon() {
      let postsArr = Object.values(this.posts);

      const index = postsArr.findIndex(
        (post) => post.uid === this.$route.params.uid
      );

      this.page = index;
      this.isDataLoaded = true;
 
    },

    nextPage() {
      this.currentPage = this.$route.params.uid;
      this.nextIndex = this.otherPages.indexOf(this.currentPage);

      this.nextIndex++;

      if (this.nextIndex >= this.amountOfPosts) {
        this.nextIndex = 0;
      }
      this.pageComingUp = this.otherPages[this.nextIndex];

      this.uidNext = this.pageComingUp;
    },
    previousPage() {
      this.currentPage = this.$route.params.uid;
      this.previousIndex = this.otherPages.indexOf(this.currentPage);

      this.previousIndex--;

      if (this.previousIndex === -1) {
        this.previousIndex = this.amountOfPosts - 1;
      }
      this.pageComingUp = this.otherPages[this.previousIndex];

      this.uidPrevious = this.pageComingUp;
    },
  },
  watch: {
    $route() {
      this.updateIcon();
    },
  },

  created() {
    this.getPosts();
  },
};
</script>

