<template>
  <div class="nav">
    <div class="container">
      <div class="white-bar d-flex justify-content-between align-items-center">
        <div class="name other-buttons">
          <router-link to="/">
            <p class="pages-button">Roxana</p>
            <p class="pages-button">Kenjeeva</p>
          </router-link>
        </div>

        <div class="flex-grow-1">
          <img src="@/img/sketch-line-left.png" class="img-fluid" alt="" />
        </div>

        <div class="menu-button other-buttons" @click="showMenu = !showMenu">
          <img
            src="@/img/menu.png"
            class="img-fluid"
            :class="{ menurotated: showMenu }"
            alt=""
          />
        </div>
        <transition name="expand">
          <div class="menu" v-if="!!showMenu" @click="showMenu = !showMenu">
            <div
              class="
                d-flex
                flex-column
                other-buttons
                justify-content-end
                flex-wrap
              "
            >
              <router-link to="/about" tag="div" class="text-end"
                ><p class="menu-item">About</p></router-link
              >
              <router-link
                to="/projects"
                tag="div"
                class="text-end my-2"
                :class="{ 'router-link-active': onProjects }"
                ><p class="menu-item">Projects</p></router-link
              >
              <router-link
                to="/shop"
                tag="div"
                class="text-end"
                :class="{ 'router-link-active': onShop }"
                ><p class="menu-item">Shop</p></router-link
              >
            </div>
          </div>
        </transition>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  data() {
    return {
      showMenu: false,
      onProjects: false,
      onShop: false,
    };
  },
  methods: {
    subIsActive() {
      const path = this.$route.path;
      if (path.includes("/projects")) {
        this.onProjects = true;
      } else {
        this.onProjects = false;
      }
      if (path.includes("/shop")) {
        this.onShop = true;
      } else {
        this.onShop = false;
      }
    },
  },
    watch: {
    $route() {
      this.subIsActive();
    },
  },
};
</script>

