<template>
  <div>
    <div v-if="desktop"><DesktopHeader /></div>
    <div v-if="!desktop"><MobileHeader /></div>

    <router-view v-slot="slotProps">
      <transition name="route" mode="out-in">
        <component :is="slotProps.Component"></component>
      </transition>
    </router-view>
  </div>
</template>
<script>
export default {
  data() {
    return {
      desktop: true,
    };
  },
  mounted() {
    this.$nextTick(() => {
      window.addEventListener("resize", () => {
        let screenSize = window.innerWidth;
        if (screenSize > 768) {
          this.desktop = true;
        } else {
          this.desktop = false;
        }
      });
      window.addEventListener("load", () => {
        let screenSize = window.innerWidth;
        if (screenSize > 768) {
          this.desktop = true;
        } else {
          this.desktop = false;
        }
      });
    });
  },
};
</script>



