<script>
export default {
  data() {
    return {
      hovering: null,
      onProjects: false,
      onShop: false,
    };
  },
  methods: {
    subIsActive() {
      const path = this.$route.path;
      if (path.includes("/projects")) {
        this.onProjects = true;
      } else {
        this.onProjects = false;
      }
      if (path.includes("/shop")) {
        this.onShop = true;
      } else {
        this.onShop = false;
      }
    },
    hoveringOver(el) {
      this.hovering = el;
    },
  },
  watch: {
    $route() {
      this.subIsActive();
    },
  },
};
</script>

