<template>
  <keep-alive>
    <div class="basket snipcart-checkout">
      <div class="row justify-content-center">
        <div class="col-12 mb-2">
          <div class="d-flex justify-content-center">
            <div>
              <img
                v-if="basketEmpty"
                src="@/img/basket.png"
                class="img-fluid basket-image"
                alt=""
              />
            </div>

            <div>
              <img
                v-if="!basketEmpty"
                src="@/img/basket-full.png"
                class="img-fluid basket-image"
                alt=""
              />
            </div>
          </div>
        </div>
      </div>
    </div>
  </keep-alive>
</template>

<script>
export default {
  name: "BasketCount",

  data() {
    return {
      basketCount: 0,
      basketEmpty: true,
    };
  },
  methods: {
    updateBasket() {
      document.addEventListener("snipcart.ready", () => {
        
        window.Snipcart.store.subscribe(() => {
          const count = window.Snipcart.store.getState().cart.items.count;
          this.basketCount = count;
          if (this.basketCount >= 1) {
            this.basketEmpty = false;
          }
        });
        window.Snipcart.events.on("item.removed", () => {
          this.updateBasket();
        });
      });
    },
  },
  mounted() {
    this.updateBasket();
  },
  beforeUpdate() {
    this.updateBasket();
  },
    watch: {
    $route() {
      this.updateBasket();
      
    },
    

  },
};
</script>

<style scoped>
.basket {
  position: fixed;
  margin: 0 auto;
  bottom: 15px;
  right: 0;
  left: 0;
  width: 40%;
  transition: all 0.2s ease;
}
.basket:hover {
  cursor: pointer;
  color: grey;
}

.basket-image {
  width: 40px;
}
</style>