<template>
  <div class="container my-0 my-md-5 pb-4">
    <div class="row justify-content-start">
      <div
        v-for="(product, index) in products"
        :key="product.id"
        :id="product.id"
        ref="post"
        :post="index"
        class="col-12 col-sm-12 col-md-12 col-lg-4 my-md-2 my-0"
      >
        <router-link
          :to="'/shop/' + product.uid"
          tag="div"
          class="row justify-content-center align-items-center project-wrapper"
        >
          <img
           
            class="img-fluid lazyload project-image p-0"
            :data-src="product.data.image.url"
            data-expand="-20"
            alt=""
            @mouseenter="addQuickFade"
            :width="product.data.image.dimensions.width"
            :height="product.data.image.dimensions.height"
          />

          <div class="text-wrapper my-2">
            <div class="col">
              <p class="title-text text-center m-0 mt-2">
                {{ product.data.name[0].text }}
              </p>
            </div>
            <div class="col">
              <p class="title-year text-center mt-2 m-0">
                €{{ product.data.price }}
              </p>
            </div>
          </div>
        </router-link>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  name: "TheShop",

  data() {
    return {
      products: [],
      linkResolver: this.$prismic.linkResolver,
      isLoaded: false,
      shop: "/shop/",
    };
  },
  methods: {
    getProducts() {
      //Query to get blog posts
      this.$prismic.client
        .query(this.$prismic.Predicates.at("document.type", "product"))
        .then((response) => {
          this.products = response.results;
        });
    },
    updateBasket() {
      
    },
    addQuickFade(e) {
      e.target.classList.add("project-image-quick-fade");
    },

  },

  created() {
    this.getProducts();
    this.updateBasket();
  },
};
</script>


